        }
    }

    .marker-ribbon {
        position: static;
        display: inline-block;
        margin-left: 0;
        line-height: 24px;
        white-space: nowrap;

        &:after {
            display: none;
        }
    }

    .top-info {
        justify-content: space-between;
        align-items: flex-start;
        margin-top: -18px;
        padding-left: 5px;

        .rounded-circle {
            border: 2px solid #fff;
        }
    }

    .course-info-block {
        .block .lb {
            width: 100px;
            display: inline-block;
            font-weight: normal;
        }

        .block .lb+span {
            font-weight: 500;
        }

        .block p {
