        padding: 5px;
    }

    .fc-event {
        border-radius: 0;
        border: 0;
        box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, .15);
    }
}

.fc-agendaDay-view {
    .fc-content {
        padding: 10px;
        font-size: 16px;
        font-weight: bold;
    }

    .box-info {
        padding: 10px;

        .txt {
            color: #000;
            margin-bottom: 10px;
            margin-top: 10px;
