        }
    }
}

.fc-list-view {
    .box-info {
        padding: 0;

        .txt {
            margin-top: 0;
            margin-bottom: 10px;
        }
    }
}

.menu-item {
    @include transition(.2s);

    &:hover,
    &.active {
        background: #f5f6fa;

        a {
            color: #001737;
            transform: none;
        }
    }
}

a.menu-link {
    @include transition(.2s);
    padding: 10px 10px;
    display: inline-block;
    color: #001737;
    line-height: 24px;
    display: flex;
    align-items: center;
}

a.menu-link svg {
    margin-right: 5px;
}

.schedule-group {
    .schedule-item {
        display: block;
        padding: 10px;
        box-shadow: 0px 0px 2px 0px rgba(0, 0, 0, .15);
        color: $primary-color-dark;
        transition: all 0.2s ease-in-out;
        margin-bottom: 10px;
        @include transition(.2s);

        &:hover {
            background: #f5f6fa;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.schedule-calendar {
    transition: all 0.3s;
}

@media (prefers-reduced-motion: reduce) {
    .schedule-calendar {
        transition: none;
    }
}

@media (min-width: 992px) {
    .schedule-calendar {
        left: 280px;
    }
}

.schedule-calendar .fc-toolbar.fc-header-toolbar {
    height: 55px;
    margin-bottom: 0;
    border-bottom: 1px solid rgba(72, 94, 144, 0.16);
}

.schedule-calendar .fc-toolbar .fc-button-group button:first-child {
    border-top-left-radius: 0.25rem;
    border-bottom-left-radius: 0.25rem;
}

.schedule-calendar .fc-toolbar .fc-button-group button:last-child {
    border-top-right-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
}

.schedule-calendar .fc-toolbar button {
    height: 32px;
    background-color: #fff;
    background-image: none;
    border: 1px solid rgba(72, 94, 144, 0.16);
    color: rgba(27, 46, 75, 0.7);
    box-shadow: none;
    padding: 0 15px;
    display: flex;
    align-items: center;
    font-size: 12px;
    text-transform: capitalize;
    outline: none;
    text-shadow: none;
    border-radius: 0;
    transition: all 0.2s ease-in-out;
}

@media (prefers-reduced-motion: reduce) {
    .schedule-calendar .fc-toolbar button {
        transition: none;
    }
}

.schedule-calendar .fc-toolbar button:hover,
.schedule-calendar .fc-toolbar button:focus {
    border-color: #8392a5;
    color: $primary-color-dark;
    position: relative;
    z-index: 5;
}

.schedule-calendar .fc-toolbar button:active {
    background-color: #f5f6fa;
}

.schedule-calendar .fc-toolbar button.fc-prev-button,
.schedule-calendar .fc-toolbar button.fc-next-button {
    padding: 0;
    width: 34px;
    justify-content: center;
}

.schedule-calendar .fc-toolbar button.fc-prev-button span,
.schedule-calendar .fc-toolbar button.fc-next-button span {
    margin: 0;
}

.schedule-calendar .fc-toolbar button.fc-state-active {
    background-color: #f5f6fa;
    border-color: #c0ccda;
    color: $primary-color-dark;
}

.schedule-calendar .fc-toolbar button.fc-today-button {
    border-radius: 0.25rem;
    color: rgba(27, 46, 75, 0.7);
    background-color: #fff;
    display: none;
}

@media (min-width: 768px) {
    .schedule-calendar .fc-toolbar button.fc-today-button {
        display: block;
    }
}

.schedule-calendar .fc-toolbar button.fc-today-button:hover,
.schedule-calendar .fc-toolbar button.fc-today-button:focus {
    border-color: #8392a5;
    color: $primary-color-dark;
}

.schedule-calendar .fc-toolbar button.fc-today-button:active {
    background-color: #f5f6fa;
}

.schedule-calendar .fc-toolbar button.fc-today-button.fc-state-disabled {
    border-color: rgba(72, 94, 144, 0.16);
    background-color: #f5f6fa;
    color: #8392a5;
    font-weight: 400;
    cursor: default;
}

.doc-item-wrap {
    overflow: hidden;
    position: relative;

    &:hover {
        .doc-link {
            transform: translateY(0);
            opacity: 1;
        }
    }

    .doc-image {
        width: 100%;

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    .doc-link {
        padding: 10px;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        background: rgba(0, 0, 0, .8);
        opacity: 0;
        @include transition(.4s);

        a {
            color: #fff;
        }

        transform:translateY(100%);
    }
}

.docs-list {
    margin: 0 -10px;

    .col-12 {
        padding: 0 10px;
    }
}

.docs-wrap {
    height: 100%;

    >.card {
        height: 100%;
    }
}

.excer-item {
    .bd-action {
        padding-top: 12px;
        padding-bottom: 20px;
        border-bottom: 1px solid $border-color;
    }

    .bd-content {
        padding-top: 20px;

        img {
            width: 100%;
        }
    }
}

span.score {
    padding: 0px 5px;
    border-radius: 4px;
    color: $primary-color;
    font-size: 16px;
    font-weight: 700;
    display: inline-block;
}

.nav-tabs .nav-link {
    background-color: rgb(245, 246, 250);
}

.tn-block {
    border: 0;
    box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
    margin-bottom: 20px;
    background: #f5f6fa;

    .right-col {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        text-align: right;
    }

    .tn-item {
        align-items: center;
    }

    .txt {
        margin-bottom: 5px;

        &.title {
            font-size: 16px;
            font-weight: 500;
            display: block;
            color: $primary-txt-color;
        }

        &.number {
            color: #8392a5;
        }

        svg {
            width: 15px;
        }
    }
}

.select2-container {
    display: block;

    .select2-search--dropdown .select2-search__field {
        &:focus {
            outline: 0;
        }
    }
}

.quiz-answer-ul {
    padding-left: 0;

    >li {
        display: block;
        counter-increment: li;
        padding: 10px 15px;
        margin-top: -1px;
        padding-left: 50px;
    }
}

.quiz-answer-ul.circlestyle>li:before {
    border-radius: 50%;
}

.quiz-answer-ul>li:before {
    content: counter(li, upper-alpha);
    font-size: 18px;
    font-weight: 600;
    line-height: 30px;
    background-color: #f5f6fa;
    display: inline-block;
    min-width: 30px;
    text-align: center;
    padding: 0 2px;
    margin-right: 10px;
    margin-left: -40px;
}

.quiz-answer-ul .checklb,
.quiz-answer-ul .radiolb,
.quiz-answer-ul .radiolb {
    display: block;
    overflow: hidden;
    margin: -10px 0;
    padding: 10px 0;
    padding-left: 40px;
    border-bottom: solid 1px #e1e1e1;
    line-height: 30px;
}

.checklb input,
.radiolb input {
    display: none;
}

.quiz-answer-ul .radiolb .ip-avata {
    padding: 5px;
}

.checklb input:checked+.ip-avata:before,
.radiolb input:checked+.ip-avata:before {
    transform: none;
    -webkit-transform: none;
    -moz-transform: none;
    -ms-transform: none;
    -o-transform: none;
}

.quiz-answer-ul label p {
    margin: 0;
}

.quiz-answer-ul .checklb .ip-avata,
.quiz-answer-ul .radiolb .ip-avata,
.quiz-answer-ul .radiolb .ip-avata {
    margin-left: -40px;
    float: left;
    width: 30px;
    height: 30px;
    border-width: 1px;
    border-color: #e1e1e1;
    border-style: solid;
}

.quiz-answer-ul .radiolb .ip-avata {
    padding: 5px;
}

.radiolb .ip-avata {
    border-radius: 50%;
    padding: 3px;
}

.checklb .ip-avata,
.radiolb .ip-avata {
    width: 24px;
    height: 24px;
    border: solid 1px #e1e1e1;
    margin-right: 10px;
    background-color: white;
    position: relative;
    overflow: hidden;
}

.radiolb .ip-avata:before {
    border-radius: 50%;
    content: '';
    width: 100%;
    height: 100%;
    margin: 0;
    background: $primary-color;
    transform: scale(0);
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
}

.checklb .ip-avata:before,
.radiolb .ip-avata:before {
    font-family: 'Font Awesome 5 Free';
    font-weight: 900;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\f00c";
    display: block;
    margin-left: 5px;
    -webkit-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
    width: 10px;
    height: 10px;
    text-align: center;
    margin-top: 0;
    color: $primary-color;
    transform: scale(0) translate(0, -40px);
    -webkit-transform: scale(0) translate(0, -40px);
    -moz-transform: scale(0) translate(0, -40px);
    -ms-transform: scale(0) translate(0, -40px);
    -o-transform: scale(0) translate(0, -40px);
}

.radiolb .ip-avata:before {
    border-radius: 50%;
    content: '';
    width: 100%;
    height: 100%;
    margin: 0;
    background: $primary-color;
    transform: scale(0);
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
}

.quiz-answer-ul .checklb .ip-avata:before,
.quiz-answer-ul .radiolb .ip-avata:before,
.quiz-answer-ul .radiolb .ip-avata:before {
    font-size: 14px;
}

.quiz-group {
    margin-bottom: 10px;
    box-shadow: 0px 0px 4px 0 rgba(0, 0, 0, .18);
}

.quiz-question {
    display: block;
    background-color: #d1e6fa;
    line-height: 20px;
    padding: 10px 15px;
    font-size: 16px;
    font-weight: 600;
}

.quiz-answer {
    margin-top: -1px;
    padding: 0px 0px 15px;
}

.quiz-fill-word {
    padding: 15px;

    &+.quiz-answer {
        border-top: 0;
    }

    p {
        margin-bottom: 0;
    }
}

.student-course {
    display: table;
    table-layout: fixed;

    >.txt {
        display: table-row;

        &:first-child {
            >span {
                padding-top: 0;
            }
        }

        >span {
            display: table-cell;
            padding: 5px 0;

            &.value {
                padding-left: 10px;
            }

            &.lb {
                font-weight: 500;
            }
        }
    }
}

.quiz-index {
    .index-link {
        display: inline-block;
        padding: 5px 0;
        width: calc(100% / 3);
        color: $primary-txt-color;

        svg {
            display: none;
        }

        &.checked {
            svg {
                display: inline-block;
                color: #00a200;
                width: 20px;
            }
        }
    }
}

.left-sticky {
    position: sticky;
    top: 75px;

    @include media(992px) {
        position: static;
    }
}

.note-block {
    padding: 15px;
    border: 1px solid $border-color;
    background: #fff;
    border-radius: 4px;
    margin-bottom: 20px;
    @include transition(.2s);

    .name {
        font-weight: 500;
    }

    .record-list {
        text-align: center;
    }

    .txt-content {
        margin-bottom: 0;
    }

    &.active {
        .comment-content {
            .txt-content {
                display: none;
            }

            .inline-edit {
                display: block;
            }
        }

        .record-action {
            display: block;
        }
    }

    .comment-content {
        .inline-edit {
            display: none;
        }
    }

    .record-action {
        display: none;
        margin-top: 15px;
    }

    &:last-child {
        margin-bottom: 0;
    }

    &:hover {
        box-shadow: rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
        border-color: transparent;
    }

    textarea {
        overflow: hidden;
        box-sizing: inherit;
        height: auto;

        &:focus {
            box-shadow: none;
            border: 2px solid #7cb2fe;
        }
    }
}

.question-wrap {
    img {
        width: 100%;
