        tr th:first-child,
        tr td:first-child {
            position: sticky;
            left: 0;
        }
    }

    &.table-fixed-right {

        tr th:last-child,
        tr td:last-child {
            position: sticky;
            right: 0;
        }
    }
}

.course-horizental {
    margin-bottom: 15px;

    .list-wrap {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    .cr-item {
        padding: 20px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.04);


        &.hd {
            padding: 10px 20px;
        }

        .badge {
            padding: 5px 10px;
            font-size: 12px;
            vertical-align: middle;
        }
    }
}

.select-with-label {
    border: 1px solid rgba(0, 0, 0, .14);
    padding-left: 10px;
    border-radius: 0.25rem;

    select {
        border: 0;

        &.custom-select:focus {
            outline: 0;
            box-shadow: none;
        }
    }

    .feather-filter {
        width: 15px;
    }
}

.post-manager {
    td.check-column {
        width: 50px;
    }
}

.post-manager {
    table.fixed {
        td {

            &.author,
            &.visibility,
            &.date {
                width: 15%;
            }

            &.banner,
            &.display {
                width: 10%;
            }
        }

        td {
            &.title-post {
                strong a {
                    text-transform: uppercase;
                }
            }
        }

        .row-actions {
            font-size: 13px;

            span {
                a {
                    color: #4f8fea;
                }
            }

            visibility: hidden;
        }

        tr:hover {
            .row-actions {
                visibility: visible;
            }
        }
    }
}

.fix-container {
    max-width: 768px;
    margin: 0 auto;
}

#calendar .event select {
    color: #000;
    margin: 0 4px;
    width: calc((100% / 2) - 8px);
    display: inline-block;
    margin-bottom: 10px;
}

#calendar .event select:last-of-type {
    width: calc(100% - 8px);
}

.config-teacher {
    overflow: hidden;

    .table {
        th {
            background: #eceff3;
        }
    }

    td.class-name {
        width: 200px;
        min-width: 200px;
        max-width: 200px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        background: #fff !important;
    }

    .select-td {
        min-width: 150px;
    }

    .teach {
        width: 60px;
        min-width: 60px;
        position: sticky;
        left: 200px;
        background: #fff;
    }

    .table-responsive {
        margin-bottom: 15px;
        overflow-x: auto;

        &:last-child {
            margin-bottom: 30px;
        }

        ::-webkit-scrollbar {
            height: 4px;
            width: 4px;
            border: 1px solid #d5d5d5;
        }

        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
}

.profile-sidebar {
    flex-shrink: 0;
}

#quick-chat-wrap {
    .sidenav {
        left: auto;
        right: 0;
        width: 350px;
        transform: translate(100%, 0);

        &.show {
            transform: translate(0, 0);
        }

        @include media(480px) {
            width: 100%;
        }
    }

    .header-nav {
        padding: 15px 10px;
        border-bottom: 1px solid #e1e1e1;

        .chat-action {
            float: left;
            margin-left: 15px;

            svg {
                width: 25px;
                height: 25px;
            }
        }

        .title {
            text-align: center;
            padding-left: 35px;
            font-size: 20px;
            color: #000;
            margin-bottom: 0;
        }
    }

    .list-people {
        list-style: none;
        padding-left: 0;
        overflow: auto;
        height: calc(100vh - 56px);

        .people-item {
            >.justify-content-end {
                margin-right: 15px;
            }

            .people-link {
                padding: 15px 20px;
                display: block;
                @include transition(.3s);

                &:hover {
                    background: #ebebeb;
                }
            }

            .info-people {
                margin-left: 10px;

                .name {
                    font-size: 16px;
                    color: #000;
                    font-weight: 600;
                    margin-bottom: 5px;
                }

                .role {
                    color: #707070;
                    margin-bottom: 0;
                    font-weight: 500;
                }
            }

            .user-content {
                padding: 15px;

                p {
                    margin-bottom: 5px;

                    &.time {
                        margin-bottom: 0;
                    }
                }

                hr {
                    margin: 5px 0;
                }

                &.bubble {
                    position: relative;
                    margin-left: 15px;
                    border-radius: .3rem;
                    background: #eceff1;

                    hr {
                        border-color: #e1e1e1;
                    }

                    &:after {
                        position: absolute;
                        top: 50%;
                        left: -9px;
                        width: 0;
                        height: 0;
                        margin-top: -20px;
                        content: '';
                        border: 10px solid transparent;
                        border-right-color: #eceff1;
                        border-left: 0;
                    }
                }

                &.bubble-right {
                    position: relative;
                    margin-left: 1rem;
                    color: #fff;
                    border-radius: .3rem;
                    background-color: #2196f3;

                    hr {
                        border-color: #fff;
                    }

                    &:after {
                        position: absolute;
                        top: 50%;
                        right: 0;
                        width: 0;
                        height: 0;
                        margin-top: -16px;
                        margin-right: -8px;
                        content: '';
                        border: 10px solid transparent;
                        border-right: 0;
                        border-left-color: #2196f3;
                    }
                }
            }

            .avatar-people {
                &.active {
                    &:after {
                        background: #60b767;
                    }
                }

                &:after {
                    content: '';
                    width: 15px;
                    height: 15px;
                    display: inline-block;
                    position: absolute;
                    background: #ccc;
                    border-radius: 50%;
                    bottom: 0px;
                    right: 0;
                    border: 2px solid #fff;
                }

                display: inline-block;
                position:relative;

                img {
                    width: 50px;
                    height: 50px;
                    object-fit: cover;
                    border-radius: 50%;
                }
            }
        }
    }
}

#chat-content-detail {
    .list-people {
        height: calc(100vh - 125px);

        .people-item {
            margin-bottom: 15px;
            padding: 10px 15px;

            .people-link {
                &:hover {
                    background: #fff;
                }
            }
        }
    }

    .chat-type {
        border-top: 1px solid #e1e1e1;
        display: flex;

        input.form-control {
            padding: 15px;
            height: auto;

            &:focus {
                outline: 0;
                box-shadow: none;
            }
        }

        .btn-send {
            padding: 0 15px;
            border-left: 1px solid #e1e1e1;
            background: #eceff1;

            svg {
                height: 50px;
                width: 30px;
            }
        }
    }
}



