.chart-on-table {
    .table {

        tr td,
        tr th {
            border: 0;
        }

        tr.row-info {
            border-top: 1px solid #e1e1e1;
        }
    }

    .collapse-row {
        >td {
            padding: 0 !important;
            border: 0 !important;
        }

        .content-table {
            display: none;
            background: rgba(231, 244, 255, 0.3);
            padding: 10px;
        }

        &.expanded {
            .content-table {
                display: block;
            }
        }
    }

    .toggle-collapse.active {
        .fa-plus-square {
            &:before {
                content: '\f146';
            }
        }
    }

    .chart-wrap {
        height: 250px;
    }

    .chart-type {
        margin-bottom: 15px;
        margin-top: 15px;

        .select-type {
            display: flex;
            align-items: center;

            >span {
                color: #000;
                font-weight: bold;
            }

            .radio {
                margin: 0 10px;

                >label {
                    margin-bottom: 0;
                }
            }
        }
    }

    td.collapse-col {
        width: 50px;
        text-align: center;
    }
}

.nowrap {
    white-space: nowrap;
}

.search-saler {
    position: relative;

    .search-saler-btn {
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);

