            color: #252525;
            stroke-width: 1px;
        }
    }
}

.create-new-status {
    .bg-overlay {
        display: none;
    }

    .status-wrap {
        color: $primary-txt-color;
        border-radius: 4px;
        @include transition(.3s);
        background-size: cover;
        background-repeat: no-repeat;

        &.active {
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 24px;
            text-align: center;
            min-height: 300px;

            textarea {
                text-align: center;

                &::placeholder {
                    color: inherit;
                }

                &:focus {
                    &::placeholder {
                        color: inherit;
                    }
                }
            }
        }
    }

    textarea {
        resize: none;
        padding: 15px;
        background: transparent;
        color: inherit;

        &:focus {
            background: transparent;
            color: inherit;
        }
    }

    .action-icons {
        >a {
            color: #b9b8b8;
            line-height: 28px;
            @include transition(.2s);

            &:hover {
                color: $primary-color;
            }
        }

        svg {
            width: 20px;
            height: 20px;
        }
    }
}

.background-demo {
    display: none;

    .demo-bg {
        padding: 5px;

        img {
            width: 40px;
            height: 40px;
            object-fit: cover;
            border-radius: 4px;
            @include transition(.3s);

            &:hover {
