}

.image-demo {
    background: #f1f0f0;
    border-radius: 3px;
    display: none;

    .list-preview {
        display: flex;
        overflow: auto;

        @include media(992px) {}

        .image-box {
            padding: 10px;
            flex: 1 1 calc(100% / 6);
            max-width: calc(100% / 6);
            position: relative;

            @include media(992px) {
                flex: 1 0 calc(100% / 5);
                max-width: calc(100% / 5);
            }

            .delete-image {
                position: absolute;
                right: 15px;
                top: 15px;
                background: #ccc;
                width: 24px;
                height: 24px;
                line-height: 24px;
                border-radius: 50%;
                color: #3463c9;
                text-align: center;
                background: rgba(255, 255, 255, .6);
                cursor: pointer;
                @include transition(.2s);

                &:hover {
                    background: rgba(0, 0, 0, .6);
                    color: #fff;
                }
            }
        }
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 4px;
    }
}

.search-post {
