        margin: 0;

        li {
            width: 25px;
            height: 25px;
            line-height: 25px;
            margin: 3px;
            text-align: center;
            display: inline-block;
            border-radius: 4px;
            transition: all 0.2s ease;
            -webkit-transition: all 0.2s ease;
            -moz-transition: all 0.2s ease;
            -o-transition: all 0.2s ease;

            a {
                color: $primary-txt-color;
                @include transition(.2s);

                &:hover {
                    color: #fff;
                    background: #a4bffb;
                }

                &.flag {
                    color: #fff;
                    background: #f95858;
                    border-radius: 50%;
                }

                &.checked {
                    border-radius: 50%;
                    background: #28b759;
                    color: #fff;
                }

                &.active {
                    background: $primary-color;
                    color: #fff;
                }

                display:block;
            }
        }
    }
}

.check-flag {
    .custom-checkbox {
        margin-bottom: 0;

        input {
            display: none;

            &:checked+span {
                &:before {
                    background: $primary-color;
                    border-color: $primary-color;
                }

                &:after {
                    color: #fff;
                    opacity: 1;
                }
            }
        }

        span {
            padding-left: 35px;
            position: relative;
            white-space: nowrap;

            &:before {
                content: '';
                width: 25px;
                height: 25px;
                border: 1px solid #e1e1e1;
                display: inline-block;
                vertical-align: middle;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translate(0, -50%);
                @include transition(.2s);
            }

            &:after {
                content: "\f00c";
                font-family: 'Font Awesome 5 Free';
                font-weight: 900;
                text-rendering: auto;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                position: absolute;
                left: 5px;
                top: 0;
                color: $primary-color;
                opacity: 0;
                @include transition(.2s);
            }
        }
    }
}

.testing {
    .test__wrap {
        .card {
            height: calc(100vh - 80px);
            display: flex;
            flex-direction: column;
        }

        .card-body {
            flex-grow: 1;

            .row {
                height: 100%;

                >.left-col,
                .right-col {
                    height: 100%;
                    overflow: auto;
                }
            }
        }

        .tab-container {
            flex-grow: 1;

            .tab-question {
                height: 100%;
                display: none;
                flex-direction: column;

                &.show {
                    display: flex;
                }
            }

        }
