.table-checkmark {
    border-collapse: collapse;

    tr td,
    tr th {
        border: 1px solid rgb(144, 144, 144);
    }

    thead tr {
        background: aliceblue;

        th {
            &:not(:first-child) {
                text-align: center;
            }
        }
    }

    tbody {
        tr {
            &:hover {
                background: #d8e5ff;
            }

            td {
                position: relative;
                min-width: 55px;

                &:first-child {
                    .number {
                        display: inline-block;
                        white-space: nowrap;
                        padding: 0 20px 0 10px;
                        font-weight: bold;
                        font-size: 1rem;
                    }
                }

                &:not(:first-child) {
                    text-align: center;
                }
            }
        }
    }
}

.tb-checkmark {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    margin-bottom: 0;

    &:hover {
        span::after {
            display: block;
            opacity: 0.3;
        }

        input {
            &:checked+span {
                &:after {
                    opacity: 1;
                }
            }
        }
    }

    input {
        display: none;

        &:checked+span {
            &:after {
                display: block;
                opacity: 1;
            }
        }
    }

    span {
        &:after {
            content: '\f00c';
            display: inline-block;
            font-size: 24px;
            font-family: 'Font Awesome 5 Free';
            color: #10b759;
            font-weight: 900;
            display: none;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            opacity: 1;
        }
    }
}

